<template>
  <div>
    Enter your phone number 
    <input type="text" v-model="phone">
    <button @click="login()" :disabled="loading">Continue</button>
    <br>
    <span v-show="hasError">{{ hasError.errors }}</span>
  </div>
</template>

<script>
import AuthService from '@/services/auth.service';

export default {
  data() {
    return {
      phone: '',
      hasError: false,
      loading: false
    }
  },
  methods: {
    login() {
      this.loading = true;
      this.hasError = false
      AuthService.login(this.phone)
        .then(res=> {
          console.log(res)
          this.$router.push({name: 'Confirm', params: { phone: res.data.data.phone}})
        })
        .catch(err=> {
          console.log(err);
          this.hasError = err.response.data;
        })
        .then(()=> {
          this.loading = false
        })
    }
  }
}
</script>